@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);

.but {
	background-color: #27CCC0;
	width: 35%;
}

.isActive::before {
	content: " ";
	position: absolute;
	left: -35px;
	display: block;
	height: 100%;
	width: 1px;
	border-left: 5px solid red;
}
.note_comment {
  border: 2px solid #F2F2F2;
  box-shadow: 0 0 4px rgba(0,0,0,0.5);
}
.note_comment h2 {
	color: #565A69;
}
.note_comment:first-child {
  margin-right: 20px;
  padding: 30px 20px 20px 30px;
  min-height: 540px;
}
.note_comment:last-child {
  padding: 20px;
  margin-right: 20px;
}
#root {
	font-family: 'Open Sans', sans-serif;
}
ul {
  list-style: none;
  padding-left: 0;
}

@media screen and (max-width: 1100px) {
	.sidebar {
		padding: 1vw 0 0 1vw !important;
	}
	.sidebar h3, h6 {
		font-size: 0.9em;
	}
}

